.header{
    display: flex;
    justify-content: space-between;
   
    flex: 1;
    padding: 12px;
    position: relative;
    align-items: center;
}
.menu{

    display: flex;
    flex: 1;
    justify-content: flex-end;

}
.menu-icon{
    font-size: 24px;

}

.mobile-menu{
    display: none;

}
.web-menu{
    display: flex;

}
.logo{
    display: flex;
    font-size: 60px;
    color:  #050505;
    font-family: 'Rampart One', cursive;
   
           
    font-weight: 800;
  
}
/* responsiveness build */
@media only screen and (max-width:720px){
    .mobile-menu{
        display:flex;
    }
    .web-menu{
        display: none;
    
    }
    .logo{
        font-size: 24px;
    }

}